.challenge-bar {
  position: relative;
  @include flex-center-column;
  padding: 16px;
  width: 100%;

  background-color: $c-warning-bg;
  @include border($c-accent);
  border-radius: $input-border-radius;

  @include onDesktop {
    @include flex-center;
    flex-direction: row;;
    width: fit-content;
  }

  &--challenge-started {
    @include flex-center-column;
    width: 100%;

    @include onDesktop {
      width: 50%;
    }
  }

  &__start-btn {
    width: 100%;

    @include onDesktop {
      width: fit-content;
    }
  }

  &__text {
    @include onDesktop {
      width: 495px;
    }
  }

  &__top {
    @include flex-spb;
    width: 100%;
  }

  &__bottom {
    @include flex-spb;
    width: 100%;
  }

  &__status-bar {
    width: 90%;
    height: 10px;

    @include border($c-accent);
    border-radius: $input-border-radius;

    &::after {
      content: "";
      display: block;
      height: 100%;
      width: 40%;

      border-radius: $input-border-radius;
      background-color: $c-accent;
    }

    &--10 {
      &::after {
        width: 10%;
      }
    }

    &--20 {
      &::after {
        width: 20%;
      }
    }

    &--30 {
      &::after {
        width: 30%;
      }
    }

    &--40 {
      &::after {
        width: 40%;
      }
    }

    &--50 {
      &::after {
        width: 50%;
      }
    }

    &--60 {
      &::after {
        width: 60%;
      }
    }

    &--70 {
      &::after {
        width: 70%;
      }
    }

    &--80 {
      &::after {
        width: 80%;
      }
    }

    &--90 {
      &::after {
        width: 90%;
      }
    }

    &--100 {
      &::after {
        width: 100%;
      }
    }
  }
}
